.causes {
	background-color : $primary;
	margin-top       : 8px;
	padding          : 24px 0 16px 0;

	@media screen and (min-width : $break-table) {
		padding : 48px 0;
	}

	@media screen and (min-width : $break-desktop) {
	}

	.container {
		max-width : none;
	}
}

.causes__wrapper {
	display               : grid;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {

		grid-template-columns : repeat(6, 1fr);
	}

	@media screen and (min-width : 1440px) {
		grid-column-gap       : 70px;
		grid-template-columns : repeat(12, 1fr);
	}
}

.causes__left {


	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
	}
}

.causes__title {
	color          : $white;
	font-size      : 16px;
	font-weight    : 500;
	grid-column    : 1/4;
	line-height    : 22px;
	margin-bottom  : 24px;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {
		font-size     : 32px;
		grid-column   : 1/7;
		line-height   : 40px;
		margin-bottom : 40px;
	}

	@media screen and (min-width : 1440px) {
		font-size     : 38px;
		grid-column   : 1/5;
		line-height   : 48px;
		margin-bottom : 0;
	}
}

.causes__slider {
	grid-column : 1/5;

	@media screen and (min-width : $break-table) {
		grid-column : 1/10;
	}

	@media screen and (min-width : 1440px) {
		grid-column : 7/13;
		grid-row    : 1/4;
	}
}

.causes__slide {
	border-radius  : 11px;
	display        : flex;
	flex-direction : column;
	overflow       : hidden;

	@media screen and (min-width : $break-table) {
		align-items    : stretch;
		flex-direction : row;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.causes__wrap {
	background-color          : $white;
	border-bottom-left-radius : 11px;
	border-top-left-radius    : 11px;
	margin-top                : -10px;
	overflow                  : hidden;
	padding                   : 16px 16px;

	@media screen and (min-width : $break-table) {
		margin-left : -10px;
		margin-top  : 0;
		padding     : 24px 24px 48px 24px;
	}

	@media screen and (min-width : 1440px) {
		padding : 24px 24px;
	}
}

.causes__img {


	@media screen and (min-width : $break-table) {
		min-width : 324px;
	}

	@media screen and (min-width : 1440px) {
		min-width : 324px;
	}

	img {
		height     : 100%;
		max-width  : none;
		object-fit : cover;
		width      : 100%;
	}
}

.causes__icon {
	height        : 40px;
	margin-bottom : 24px;
	width         : 40px;

	@media screen and (min-width : $break-table) {
		height        : 48px;
		margin-bottom : 48px;
		width         : 48px;
	}

	@media screen and (min-width : 1440px) {

	}
}

.causes__subtitle {
	color          : $primary;
	display        : block;
	font-size      : 16px;
	font-weight    : 500;
	line-height    : 20px;
	margin-bottom  : 16px;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}

}

.causes__text {
	color       : $primary;
	font-size   : 12px;
	font-weight : 500;
	line-height : 16px;

	@media screen and (min-width : $break-table) {
		font-size   : 14px;
		line-height : 20px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.swiper--causes {
	.swiper-slide {
		width : 100%;
	}
}


.causes__nav {
	align-items     : center;
	display         : flex;
	grid-column     : 1/5;
	grid-row        : 3/3;
	justify-content : center;
	margin-top      : 8px;

	@media screen and (min-width : $break-table) {
		grid-column     : 1/7;
		justify-content : space-between;
		margin-top      : 40px;
	}

	@media screen and (min-width : 1440px) {
		grid-column : 1/3;
		margin-top  : 0;
	}

	.swiper-nav {
		display : none;

		@media screen and (min-width : $break-table) {
			display : flex;
		}
	}

	.swiper-pagination {
		display    : flex;
		margin-top : 8px;
		position   : unset;

		@media screen and (min-width : 1440px) {
			margin-left : 48px;
			margin-top  : 0;
		}
	}
}
