.examination {
	background-color : #EFF8F8;
	margin-top       : 8px;
}

.examination__wrapper {

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {

		align-items : stretch;
		display     : flex;
	}
}


.examination__about {
	padding : 24px 16px;
	width   : 100%;

	@media screen and (min-width : $break-table) {
		padding : 48px 48px;
	}
	@media screen and (min-width : $break-desktop) {
		max-width : 657px;
		padding   : 80px 48px 48px 48px;
	}
}

.examination__title {
	margin-bottom  : 16px;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {
		margin-bottom : 40px;
	}
	@media screen and (min-width : $break-desktop) {
		margin-bottom : 48px;
	}

}

.examination__list {

}

.examination__item {
	align-items : center;
	display     : flex;

	&:not(:last-child) {
		margin-bottom : 16px;
	}

	@media screen and (min-width : $break-table) {
		&:not(:last-child) {
			margin-bottom : 24px;
		}
	}
	@media screen and (min-width : $break-desktop) {

	}
}

.examination__icon {
	flex-shrink  : 0;
	margin-right : 16px;

	@media screen and (min-width : $break-table) {
		margin-right : 24px;
	}
	@media screen and (min-width : $break-desktop) {

	}

}

.examination__text {
	color       : $primary;
	font-size   : 12px;
	font-weight : 500;
	line-height : 16px;

	@media screen and (min-width : $break-table) {
		font-size   : 14px;
		line-height : 20px;
	}
	@media screen and (min-width : $break-desktop) {

	}
}

.examination__image {
	border-radius : 12px;
	overflow      : hidden;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		border-bottom-left-radius  : 12px;
		border-bottom-right-radius : 0;
		border-top-left-radius     : 12px;
		border-top-right-radius    : 0;
		width                      : 50%;
	}

	img {
		height     : 100%;
		max-width  : none;
		object-fit : cover;
		width      : 100%;
	}
}
