.header {
	background-color : $secondary;
	border-radius    : 12px 12px 0 0;
	padding-top      : 16px;

	@media screen and (min-width : $break-table) {
		padding-top : 22px;
	}
	@media screen and (min-width : $break-desktop) {

	}
}

.header__container {
	max-width : none;
}

.header__top {
	align-items     : center;
	border-bottom   : 1px solid #BFD2D5;
	display         : flex;
	justify-content : space-between;
	padding-bottom  : 16px;

	@media screen and (min-width : $break-table) {
		padding-bottom : 24px;
	}
	@media screen and (min-width : $break-desktop) {

	}
}

.logo {
	align-items      : center;
	background-color : $white;
	border-radius    : 8px;
	display          : flex;
	filter           : drop-shadow(0px 0px 4px rgba(0, 42, 53, 0.04)) drop-shadow(0px 8px 16px rgba(0, 42, 53, 0.08));
	height           : 41px;
	justify-content  : center;
	width            : 41px;

	img {
		flex-shrink : 0;
	}
}

.box-icon {
	align-items      : center;
	background-color : $white;
	border-radius    : 46px;
	display          : flex;
	filter           : drop-shadow(0px 0px 4px rgba(0, 42, 53, 0.04)) drop-shadow(0px 8px 16px rgba(0, 42, 53, 0.08));
	height           : 40px;
	justify-content  : center;
	transition       : background-color .3s ease;
	width            : 40px;

	&:hover {
		background-color : $primary;

		svg {
			path {
				fill       : $white;
				transition : fill .3s ease;
			}
		}
	}

	img {
		flex-shrink : 0;
	}

	svg {
		path {
			fill       : $primary;
			transition : fill .3s ease;
		}
	}
}

.header__start {
	align-items : center;
	display     : flex;
}

.header__info {
	color       : $primary;
	display     : none;
	font-size   : 12px;
	line-height : 15px;
	margin-left : 16px;

	@media screen and (min-width : $break-table) {
		display : block;
	}
	@media screen and (min-width : $break-desktop) {

	}
}

.s-block {

	height     : 0;
	opacity    : 0;
	visibility : hidden;
	width      : 0;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		align-items : center;
		display     : flex;
		height      : auto;
		opacity     : 1;
		visibility  : visible;
		width       : auto;
	}

	a {
		&:not(:first-child) {
			margin-left : 8px;
		}
	}
}

.header__num {
	display : flex;
}

.number {
	align-items : center;
	color       : $primary;
	display     : flex;
	font-size   : 0;
	font-weight : 700;
	line-height : 17px;

	@media screen and (min-width : $break-table) {
		font-size : 14px;
	}
	@media screen and (min-width : $break-desktop) {

	}

	span {

		@media screen and (min-width : $break-table) {
			margin-right : 16px;
		}
		@media screen and (min-width : $break-desktop) {

		}

	}
}

.header__panel {
	background-color : #EFF8F8;
	height           : 101vh;
	position         : absolute;
	right            : -100%;
	top              : 0;
	transition       : right .3s ease-in;
	width            : 100%;
	z-index          : 100;

	&.active {
		right : 0;
	}

	@media screen and (min-width : $break-table) {
		border-radius : 16px 0 0 16px;
		max-width     : 480px;
	}
	@media screen and (min-width : $break-desktop) {
		border-bottom : 1px solid #BFD2D5;
		display       : block;
		height        : auto;
		max-width     : none;
		padding       : 24px 0;
		position      : unset;
		right         : unset;
		top           : unset;
	}
}

.header__wrapper {
	height     : 100%;
	overflow-y : auto;
	padding    : 24px 24px;
	width      : 100%;

	@media screen and (min-width : $break-table) {


	}


	@media screen and (min-width : $break-desktop) {
		margin    : 0 auto;
		max-width : 1056px;
		padding   : 0;
	}
}

.nav {
	border-bottom  : 1px solid #BFD2D5;
	padding-bottom : 32px;

	@media screen and (min-width : $break-desktop) {
		border-bottom  : none;
		padding-bottom : 0;
	}


	&__list {
		display        : flex;
		flex-direction : column;

		@media screen and (min-width : $break-table) {

		}
		@media screen and (min-width : $break-desktop) {
			align-items     : center;
			flex-direction  : row;
			justify-content : space-between;

		}

	}

	&__item {

		&:not(:last-child) {
			margin-bottom : 24px;
		}

		@media screen and (min-width : $break-table) {

		}
		@media screen and (min-width : $break-desktop) {
			&:not(:last-child) {
				margin-bottom : 0;
			}
		}
	}

	&__link {
		color       : $primary;
		font-size   : 16px;
		font-weight : 500;
		line-height : 24px;
		position    : relative;

		@media screen and (min-width : $break-table) {
			font-size   : 14px;
			line-height : 20px;
		}
		@media screen and (min-width : $break-desktop) {

		}

		&:before {
			background-color : $primary;
			bottom           : 0;
			content          : "";
			height           : 1px;
			position         : absolute;
			right            : 0;
			transition       : width .3s ease;
			width            : 0;
			z-index          : 1;
		}

		&:hover {
			&:before {
				left  : 0;
				right : unset;
				width : 100%;
			}
		}
	}
}

.burger {
	border      : none;
	margin-left : 16px;
	outline     : none;

	&:hover {
		background-color : $white;
	}

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		display : none;
	}

	img {
		pointer-events : none;
	}
}

.close-menu {
	background-color : transparent;
	border           : none;
	height           : 24px;
	padding          : 0;
	width            : 24px;

	svg {
		circle {
			fill       : transparent;
			transition : fill .3s ease;
		}
	}

	&:hover {
		svg {
			circle {
				fill       : #CDE8EA;
				transition : fill .3s ease;
			}
		}
	}
}

.header__mob {
	align-items     : center;
	display         : flex;
	justify-content : space-between;
	margin-bottom   : 32px;

	@media screen and (min-width : $break-desktop) {
		display : none;
	}
}

.header__overlay {
	&.active {
		background-color : rgba(0, 0, 0, 0.6);
		bottom           : 0;
		left             : 0;
		position         : fixed;
		right            : 0;
		top              : 0;
		transition       : opacity 0.5s ease;
		z-index          : 100;
	}

}

.header__wrap {
	align-items : center;
	display     : flex;

	span {
		color          : $primary;
		display        : flex;
		font-size      : 10px;
		font-weight    : 500;
		line-height    : 12px;
		margin-left    : 16px;
		max-width      : 112px;
		text-transform : uppercase;
	}
}

.contacts-list--mob {
	padding-bottom : 32px;
	padding-top    : 32px;


	@media screen and (min-width : $break-table) {
		grid-row-gap          : 16px;
		grid-template-columns : repeat(6, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		display : none;
	}

	.contacts-list__item {
		align-items : start;
		display     : flex;
	}

	.contacts-list__icon {
		margin-bottom : 0;
	}

	.contacts-list__wrap {
		display        : flex;
		flex-direction : column;
		margin-left    : 16px;
	}
}

.header__bottom {
	align-items     : flex-end;
	display         : flex;
	justify-content : space-between;

	@media screen and (min-width : $break-desktop) {
		display : none;
	}
}

.copyright {
	color       : #5E7B83;
	font-size   : 10px;
	font-weight : 400;
	line-height : 14px;
}

.s-block--mob {
	display    : flex;
	height     : auto;
	opacity    : 1;
	visibility : visible;
	width      : auto;
}
