.partners {
	background-color : $primary;
	margin-top       : 8px;
	padding          : 24px 0;

	@media screen and (min-width : $break-table) {
		padding : 48px 0;
	}

	@media screen and (min-width : $break-desktop) {

	}

	.container {
		max-width : none;
	}
}

.partners__wrapper {
	margin-bottom : 32px;

	@media screen and (min-width : $break-table) {
		align-items     : flex-start;
		display         : flex;
		justify-content : space-between;
		margin-bottom   : 64px;
	}

	@media screen and (min-width : $break-desktop) {

	}

}

.partners__title {
	color         : $white;
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 0;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.partners__text {
	color       : $white;
	font-size   : 12px;
	font-weight : 500;
	line-height : 16px;

	@media screen and (min-width : $break-table) {
		font-size   : 14px;
		line-height : 20px;
		max-width   : 432px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.partners__bottom {
	margin     : 0 -16px;
	overflow-x : auto;

	@media screen and (min-width : $break-table) {
		margin : 0 -48px;
	}

	@media screen and (min-width : $break-desktop) {
		margin    : 0 auto;
		max-width : 1039px;
	}
}

.partners__list {
	align-items : center;
	display     : flex;
	min-width   : 929px;
	padding     : 0 16px;
	@media screen and (min-width : $break-table) {
		justify-content : space-between;
	}
}

.partners__item {
	align-items : center;
	display     : flex;
	flex-shrink : 0;

	&:not(:first-child) {
		margin-left : 24px;
	}

	@media screen and (min-width : $break-table) {

	}
}
