.contacts {
	background-color : $secondary;
	margin-top       : 8px;
	padding          : 24px 0;

	@media screen and (min-width : $break-table) {
		padding : 48px 0;
	}

	@media screen and (min-width : $break-desktop) {

	}

	.container {
		max-width : none;
	}
}

.contacts__title {
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 48px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.contacts-list {
	display               : grid;
	grid-gap              : 16px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-gap              : 24px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.contacts-list__item {
	background-color : $white;
	border-radius    : 12px;
	grid-column      : 4 span;
	padding          : 16px 16px;
	transition       : background-color .3s ease;

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
		padding     : 24px 24px;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 3 span;
	}

	&:hover {
		background-color : #CDE8EA;

		svg {
			rect {
				fill       : $white;
				transition : fill .3s ease;
			}
		}
	}
}

.contacts-list__icon {
	border-radius : 100%;
	height        : 40px;
	margin-bottom : 24px;
	width         : 40px;

	@media screen and (min-width : $break-table) {
		height        : 48px;
		margin-bottom : 48px;
		width         : 48px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	svg {
		rect {
			fill       : #CDE8EA;
			transition : fill .3s ease;
		}
	}
}

.contacts-list__name {
	color         : $primary;
	display       : block;
	font-size     : 10px;
	font-weight   : 500;
	line-height   : 14px;
	margin-bottom : 8px;
	opacity       : 0.6;

	@media screen and (min-width : $break-table) {
		font-size   : 14px;
		line-height : 20px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.contacts-list__info {
	color       : $primary;
	font-size   : 12px;
	font-weight : 600;
	line-height : 16px;

	@media screen and (min-width : $break-table) {
		font-size   : 16px;
		line-height : 24px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}
