// Arrow
.swiper-button-prev,
.swiper-button-next {
	align-items                 : center;
	background-color            : $white;
	border-radius               : 100%;
	cursor                      : pointer;
	display                     : flex;
	height                      : 40px;
	justify-content             : center;
	outline                     : 0;
	position                    : relative;
	-webkit-tap-highlight-color : transparent;
	transition                  : opacity .3s ease;
	user-select                 : none;
	vertical-align              : top;
	width                       : 40px;

	&:after {
		content  : '';
		left     : 0;
		position : absolute;
		top      : 0;
	}

	@media screen and (min-width : 1076px) {
		&:hover {
			opacity : .7;
		}
	}

	&.swiper-button-disabled {
		cursor         : auto;
		opacity        : 0.3;
		pointer-events : none;
	}
}

.swiper-button-prev {
	margin-right : 2px;

}

.swiper-button-next {
	margin-left : 2px;
}

.slider__pagination {
	display         : flex;
	flex-flow       : row nowrap;
	justify-content : space-between;
}


.swiper-nav {
	display : flex;
}
