.section {
	margin : 42px 0;
	@media screen and (min-width : $break-table) {
		margin : 62px 0;
	}
	@media screen and (min-width : $break-desktop) {
		margin : 105px 0;
	}
}

.title {
	font-size      : 16px;
	font-style     : normal;
	font-weight    : 500;
	line-height    : 22px;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {
		font-size   : 32px;
		line-height : 40px;
	}
	@media screen and (min-width : $break-desktop) {
		font-size   : 40px;
		line-height : 48px;
	}
}
