.about {
	background-color : $primary;
	margin-top       : 8px;
}

.about__wrapper {
	display               : grid;
	grid-gap              : 16px;
	grid-template-columns : repeat(4, 1fr);
	overflow              : hidden;
	padding               : 24px 16px;

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(8, 1fr);
		padding               : 48px 48px;
	}
	@media screen and (min-width : $break-desktop) {
		grid-gap              : 24px;
		grid-template-columns : repeat(12, 1fr);
		grid-template-rows    : repeat(2, 1fr);
	}
}

.about__title {
	color          : $white;
	font-size      : 16px;
	font-weight    : 500;
	grid-column    : 1/4;
	grid-row       : 1/1;
	line-height    : 22px;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {
		font-size   : 32px;
		grid-column : 1/5;
		grid-row    : 1/2;
		line-height : 40px;
	}
	@media screen and (min-width : $break-desktop) {
		grid-column   : 1/4;
		margin-bottom : 0;
	}
}


.about__item {
	background-color : $white;
	border-radius    : 12px;
	padding          : 24px 24px;

	@media screen and (min-width : $break-table) {
		display         : flex;
		flex-direction  : column;
		justify-content : center;
		padding         : 40px 40px;
	}
	@media screen and (min-width : $break-desktop) {
		min-height : 200px;
	}

	&:nth-of-type(1) {
		grid-column : 1/3;

		@media screen and (min-width : $break-table) {
			grid-column : 6/9;
			grid-row    : 1/3;
		}
		@media screen and (min-width : $break-desktop) {
			grid-column : 7/10;
			grid-row    : 1/1;
		}

		.about__text {
			@media screen and (min-width : $break-desktop) {
				max-width : 70px;
			}
		}

	}

	&:nth-of-type(2) {
		grid-column : 1/3;

		@media screen and (min-width : $break-table) {
			grid-column : 6/9;
			grid-row    : 3/4;
		}
		@media screen and (min-width : $break-desktop) {
			grid-column : 10/13;
			grid-row    : 1/1;
		}

		.about__text {
			@media screen and (min-width : $break-desktop) {
				max-width : 88px;
			}
		}
	}

	&:nth-of-type(3) {
		grid-column : 1/5;
		grid-row    : 3/4;

		@media screen and (min-width : $break-table) {
			grid-column : 6/9;
			grid-row    : 4/4;
		}
		@media screen and (min-width : $break-desktop) {
			grid-column : 7/13;
			grid-row    : 2/2;
		}
	}

	.about__text {
		color       : $primary;
		font-size   : 12px;
		font-weight : 500;
		line-height : 16px;

		@media screen and (min-width : $break-table) {

		}
		@media screen and (min-width : $break-desktop) {
			font-size   : 16px;
			line-height : 24px;
		}
	}
}

.about__num {
	color         : $primary;
	font-size     : 28px;
	font-weight   : 500;
	line-height   : 32px;
	margin-bottom : 8px;

	@media screen and (min-width : $break-table) {
		font-size   : 40px;
		line-height : 48px;
	}
	@media screen and (min-width : $break-desktop) {
		font-size   : 56px;
		line-height : 64px;
	}

}

.about__title {

}

.about__text {
	color          : $white;
	display        : flex;
	flex-direction : column;
	font-size      : 12px;
	font-weight    : 500;
	grid-column    : 1/5;
	line-height    : 16px;

	@media screen and (min-width : $break-table) {
		grid-column : 1/5;
		grid-row    : 2/2;
	}
	@media screen and (min-width : $break-desktop) {
		font-size       : 14px;
		grid-column     : 1/4;
		grid-row        : 2/2;
		justify-content : flex-end;
		line-height     : 20px;
	}
}

.about__image {
	bottom      : -24px;
	display     : block;
	grid-column : 3/5;
	grid-row    : 4/6;
	margin-top  : auto;
	position    : relative;
	text-align  : center;

	@media screen and (min-width : $break-table) {
		bottom      : -48px;
		grid-column : 1/6;
		grid-row    : 3/5;
	}
	@media screen and (min-width : $break-desktop) {
		bottom        : 0;
		grid-column   : 4/7;
		grid-row      : 1/4;
		margin-bottom : -79px;
	}

	img {
		height     : 214px;
		max-width  : none;
		object-fit : cover;
		width      : 114px;

		@media screen and (min-width : $break-table) {
			height : 380px;
			width  : 202px;
		}

		@media screen and (min-width : $break-desktop) {
			height     : 100%;
			object-fit : contain;
			width      : 247px;
		}
	}
}
