.mrt-needed {
	background-color : $primary;
	margin-top       : 8px;
	padding          : 24px 0 16px 0;

	@media screen and (min-width : $break-table) {
		padding : 48px 0;
	}
	@media screen and (min-width : $break-desktop) {

	}

	.container {
		max-width : none;
	}
}


.mrt-needed__grid {
	display               : grid;
	grid-gap              : 16px;
	grid-template-columns : repeat(3, 1fr);

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(6, 1fr);
	}
	@media screen and (min-width : $break-desktop) {
		grid-gap              : 24px;
		grid-template-columns : repeat(12, 1fr);
	}
}

.mrt-needed__about {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 1/7;
	}
	@media screen and (min-width : $break-desktop) {
		grid-column : 1/7;
	}
}

.mrt-needed__item {
	background-color : $white;
	border-radius    : 11px;
	cursor           : pointer;
	display          : flex;
	flex-direction   : column;
	grid-column      : 3 span;
	justify-content  : space-between;
	padding          : 16px 16px;
	transition       : background-color .3s ease, fill .3s ease;

	@media screen and (min-width : $break-table) {
		padding : 24px 24px;
	}
	@media screen and (min-width : $break-desktop) {
		min-height : 185px;
	}

	&:hover {
		background-color : #CDE8EA;

		.mrt-needed__icon {

			svg {
				rect {
					fill       : $white;
					transition : fill .3s ease;
				}
			}
		}
	}

	&.active {
		background-color : #CDE8EA;

		.mrt-needed__icon {

			svg {
				rect {
					fill       : $white;
					transition : fill .3s ease;
				}
			}
		}
	}

}

.mrt-needed__icon {
	margin-bottom : 24px;
	@media screen and (min-width : $break-table) {
		margin-bottom : 48px;
	}
	@media screen and (min-width : $break-desktop) {

	}

	svg {
		transition : fill .3s ease;

		rect {
			fill       : #CDE8EA;
			transition : fill .3s ease;
		}

		path {
			fill : #003B4A;
		}
	}
}

.mrt-needed__title {
	color          : $white;
	max-width      : 250px;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {
		max-width : 550px;
	}
	@media screen and (min-width : $break-desktop) {

	}
}

.mrt-needed__subtitle {
	color          : $white;
	display        : block;
	font-family    : 'Proxima Nova';
	font-size      : 12px;
	font-style     : normal;
	font-weight    : 300;
	line-height    : 18px;
	margin-bottom  : 8px;
	opacity        : 0.6;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {
		font-size     : 16px;
		line-height   : 24px;
		margin-bottom : 16px;
	}
	@media screen and (min-width : $break-desktop) {

	}
}

.mrt-needed__text {
	color       : $primary;
	font-size   : 12px;
	font-weight : 500;
	line-height : 16px;

	@media screen and (min-width : $break-table) {
		font-size   : 14px;
		line-height : 20px;
	}
	@media screen and (min-width : $break-desktop) {

	}
}

.mrt-needed__item--enroll {
	background-color : #CDE8EA;
	border           : 3px solid transparent;
	padding          : 21px 21px;

	.mrt-needed__text {
		color          : $primary;
		font-size      : 16px;
		font-weight    : 500;
		line-height    : 22px;
		max-width      : 124px;
		text-transform : uppercase;

		@media screen and (min-width : $break-table) {
			font-size   : 24px;
			line-height : 32px;
			max-width   : 183px;
		}
		@media screen and (min-width : $break-desktop) {

		}

	}

	.mrt-needed__icon {
		align-items     : flex-end;
		display         : flex;
		flex-direction  : column;
		justify-content : flex-end;
		margin-bottom   : 0;
		text-align      : right;
	}

	&:hover {
		background-color : transparent;
		border           : 3px solid #FFF;

		.mrt-needed__text {
			color : $white;
		}

		svg {
			circle {
				fill   : transparent;
				stroke : white;
			}

			path {
				fill   : white;
				stroke : white;
			}

		}
	}

	&.active {
		background-color : transparent;
		border           : 3px solid #FFF;

		.mrt-needed__text {
			color : $white;
		}

		svg {
			circle {
				fill   : transparent;
				stroke : white;
			}

			path {
				fill   : white;
				stroke : white;
			}

		}
	}
}

.mrt-modal {
	&__icon {
		height        : 40px;
		margin-bottom : 40px;
		width         : 40px;

		@media screen and (min-width : $break-table) {
			height        : 48px;
			margin-bottom : 32px;
			width         : 48px;
		}
		@media screen and (min-width : $break-desktop) {
			margin-bottom : 48px;
		}
	}

	&__title {
		color          : $primary;
		font-size      : 12px;
		font-weight    : 500;
		line-height    : 16px;
		margin-bottom  : 16px;
		text-transform : uppercase;

		@media screen and (min-width : $break-table) {
			font-size   : 18px;
			line-height : 28px;
		}
		@media screen and (min-width : $break-desktop) {
			font-size     : 22px;
			line-height   : 32px;
			margin-bottom : 24px;
		}
	}

	&__text {
		color       : $primary;
		font-size   : 12px;
		font-weight : 500;
		line-height : 16px;

		@media screen and (min-width : $break-table) {
			font-size   : 14px;
			line-height : 20px;
		}
		@media screen and (min-width : $break-desktop) {
			font-size   : 16px;
			line-height : 24px;
		}
	}
}
