.sliders {
	margin-top : 8px;
}

.sliders__wrapper {
	align-items           : flex-start;
	display               : grid;
	grid-gap              : 8px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(6, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-template-columns : repeat(12, 1fr);
	}
}

.equipment {
	background-color : $primary;
	border-radius    : 12px;
	grid-column      : 4 span;
	height           : 100%;
	padding          : 24px 16px 48px 16px;
	position         : relative;

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
		padding     : 48px 48px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	.swiper-pagination {
		bottom    : 17px;
		left      : 50%;
		transform : translateX(-50%);
		@media screen and (min-width : $break-table) {
			display : none;
		}

	}
}

.equipment__head {
	align-items     : center;
	display         : flex;
	justify-content : space-between;
	margin-bottom   : 24px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 52px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.equipment__nav {
	display : none;

	@media screen and (min-width : $break-table) {
		display : flex;
	}
}

.equipment__title {
	color          : $white;
	font-size      : 16px;
	font-weight    : 500;
	line-height    : 22px;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {
		font-size   : 24px;
		line-height : 32px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.equipment__img {
	align-items      : center;
	background-color : $white;
	border-radius    : 12px;
	border-radius    : 12px;
	display          : flex;
	justify-content  : center;
	margin-bottom    : 16px;
	min-height       : 240px;
	overflow         : hidden;
	padding          : 21px 0;

	@media screen and (min-width : $break-table) {
		margin-bottom : 24px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	img {
		height     : 100%;
		max-width  : none;
		object-fit : cover;
		width      : 100%;
	}
}

.equipment__name {
	color         : $white;
	display       : block;
	font-size     : 12px;
	font-weight   : 400;
	line-height   : 16px;
	margin-bottom : 8px;

	@media screen and (min-width : $break-table) {
		font-size   : 14px;
		line-height : 20px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.equipment__model {
	color          : $white;
	font-size      : 16px;
	font-weight    : 600;
	line-height    : 22px;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {
		font-size   : 24px;
		line-height : 32px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.swiper--equipment {
	border-radius : 12px;
}

.doctors-wrapper {
	grid-column : 4 span;
	position    : relative;

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
	}

	@media screen and (min-width : $break-desktop) {

	}

	.swiper-pagination {
		left      : 50%;
		transform : translateX(-50%);

		.swiper-pagination-bullet {
			border : 1px solid #93A1A3;
		}

		.swiper-pagination-bullet.swiper-pagination-bullet-active {
			background-color : $primary;
		}


		@media screen and (min-width : $break-table) {
			display : none;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.doctors {
	background-color : $primary;
	border-radius    : 12px;
	display          : flex;
	flex-direction   : column;
	height           : 100%;
	justify-content  : space-between;
	overflow         : hidden;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.doctors__head {
	align-items     : center;
	display         : flex;
	justify-content : space-between;
	padding         : 24px 16px 0 16px;
	position        : relative;
	z-index         : 10;

	@media screen and (min-width : $break-table) {
		padding : 48px 48px 0 48px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.doctors__title {
	color          : $white;
	font-size      : 16px;
	font-weight    : 500;
	line-height    : 22px;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {
		font-size   : 24px;
		font-weight : 400;
		line-height : 32px;

	}

	@media screen and (min-width : $break-desktop) {
		max-width : 174px;
	}
}

.doctors__item {
	display         : flex;
	flex-direction  : column;
	justify-content : flex-end;
	padding-top     : 16px;
	position        : relative;
	text-align      : center;
}

.doctors__wrap {
	align-items      : flex-start;
	background-color : $white;
	border-radius    : 12px;
	bottom           : 16px;
	display          : flex;
	flex-direction   : column;
	left             : 50%;
	min-width        : 236px;
	padding          : 16px 16px;
	position         : absolute;
	transform        : translateX(-50%);
	z-index          : 1;

	@media screen and (min-width : $break-table) {
		align-items     : end;
		bottom          : 48px;
		flex-direction  : row;
		justify-content : space-between;
		min-width       : 550px;
		padding         : 24px 24px;
		text-align      : start;
	}

	@media screen and (min-width : $break-desktop) {
		min-width : 400px;
	}

	@media screen and (min-width : 1400px) {
		min-width : calc(100% - 130px);
	}

	.b-btn--dark {
		font-size   : 10px;
		line-height : 14px;
		width       : 100%;
		@media screen and (min-width : $break-table) {
			font-size   : 16px;
			line-height : 24px;
			width       : auto;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.doctors__nav {
	display : none;

	@media screen and (min-width : $break-table) {
		display : block;
	}

}

.doctors__info {
	margin-bottom : 16px;
	text-align    : left;

	@media screen and (min-width : $break-table) {
		margin-bottom : 0;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.doctors__image {
	display         : flex;
	flex-direction  : column;
	justify-content : flex-end;


	@media screen and (min-width : $break-table) {
		margin-top : -70px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-top : -60px;
	}

	img {
		height     : 293px;
		max-width  : none;
		object-fit : contain;
		position   : relative;
		width      : 253px;

		@media screen and (min-width : $break-table) {
			height : 100%;
			left   : 120px;
			width  : 300px;
		}

		@media screen and (min-width : $break-desktop) {
			left : 30px;
		}

	}
}

.doctors__speciality {
	color          : $primary;
	display        : block;
	font-size      : 10px;
	font-weight    : 500;
	line-height    : 14px;
	margin-bottom  : 8px;
	opacity        : 0.6;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {
		font-size     : 12px;
		line-height   : 16px;
		margin-bottom : 13px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.doctors__name {
	color          : $primary;
	display        : block;
	font-size      : 16px;
	font-weight    : 600;
	line-height    : 22px;
	margin-bottom  : 4px;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {
		font-size   : 24px;
		line-height : 32px;
		max-width   : 186px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.doctors__experience {
	color       : $primary;
	font-size   : 10px;
	font-weight : 400;
	line-height : 14px;

	@media screen and (min-width : $break-table) {
		font-size   : 14px;
		line-height : 20px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.swiper--doctor {

	@media screen and (min-width : $break-table) {
		overflow : visible;
	}

	.swiper-slide {

		width : 100%;
	}
}
