.health {
	background-color : $secondary;
	margin-top       : 8px;
}

.health__container {
	display        : flex;
	flex-direction : column-reverse;

	@media screen and (min-width : $break-table) {
		align-items    : center;
		flex-direction : row;
	}
	@media screen and (min-width : $break-desktop) {

	}
}

.health__info {
	padding : 0 16px 24px 16px;

	@media screen and (min-width : $break-table) {
		display         : flex;
		flex-direction  : column;
		justify-content : space-between;
		padding         : 48px 20px 48px 48px;
		width           : 55%;
	}

	@media screen and (min-width : $break-desktop) {
		justify-content : space-between;
		padding         : 48px 0 48px 64px;
	}
}

.health__image {
	text-align : center;

	@media screen and (min-width : $break-table) {
		max-width  : 100%;
		text-align : right;
	}

	@media screen and (min-width : $break-desktop) {
		min-width : 562px;
	}

}

.health__title {
	margin-bottom : 32px;
	max-width     : 207px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 40px;
		max-width     : 500px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 48px;
	}
}

.health__wrap {
	.b-btn--dark {
		font-size     : 10px;
		font-weight   : 600;
		line-height   : 16px;
		margin-bottom : 16px;
		padding       : 12px 30px;
		width         : 100%;

		@media screen and (min-width : 480px) {
			width : auto;
		}

		@media screen and (min-width : $break-table) {
			font-size     : 16px;
			line-height   : 24px;
			margin-bottom : 24px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.health__subtitle {
	color         : $primary;
	display       : block;
	font-size     : 12px;
	font-weight   : 500;
	line-height   : 16px;
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {
		font-size     : 16px;
		line-height   : 24px;
		margin-bottom : 24px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.health__text {
	color       : $primary;
	font-size   : 9px;
	font-weight : 500;
	line-height : 12px;

	@media screen and (min-width : $break-table) {
		font-size   : 12px;
		line-height : 16px;
		max-width   : 329px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}


