.footer {
	padding-top : 16px;

	@media screen and (min-width : $break-table) {
		padding-top : 24px;
	}
	@media screen and (min-width : $break-desktop) {

	}
}

.footer__wrapper {
	align-items    : center;
	display        : flex;
	flex-direction : column;

	@media screen and (min-width : $break-table) {
		flex-direction  : row;
		justify-content : space-between;
	}
	@media screen and (min-width : $break-desktop) {

	}
}

.footer__info {
	color       : #5E7B83;
	font-size   : 12px;
	font-weight : 400;
	line-height : 15px;

	&:not(:last-child) {
		margin-bottom : 8px;
	}

	@media screen and (min-width : $break-table) {
		font-size   : 12px;
		line-height : 15px;

		&:not(:last-child) {
			margin-bottom : 0;
		}
	}
}
