.mrt {
	background-color : $secondary;
	margin-top       : 30px;
	padding          : 24px 0;

	@media screen and (min-width : $break-table) {
		margin-top : 8px;
		padding    : 48px 0;
	}
	@media screen and (min-width : $break-desktop) {

	}
}

.mrt__container {
	
}

.mrt__wrapper {


	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		align-items     : center;
		display         : flex;
		justify-content : space-between;
	}
}

.mrt__img {

	margin     : 0 auto 16px auto;
	max-width  : 186px;
	text-align : center;

	@media screen and (min-width : $break-table) {
		margin    : 0 0 50px 0;
		max-width : none;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 0;
		text-align    : start;
	}
}

.mrt__about {


	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		max-width    : 418px;
		padding-left : 30px;
	}
}

.mrt__title {
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 24px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.mrt__text {
	color       : $primary;
	font-size   : 12px;
	font-weight : 500;
	line-height : 18px;

	&:not(:last-child) {
		margin-bottom : 16px;
	}

	@media screen and (min-width : $break-table) {
		font-size   : 16px;
		line-height : 24px;

		&:not(:last-child) {
			margin-bottom : 24px;
		}
	}

	@media screen and (min-width : $break-desktop) {

	}


}
