.services {
	background-color : $secondary;
	margin-top       : 8px;
	padding          : 24px 0 32px 0;
	position         : relative;

	@media screen and (min-width : $break-table) {
		padding : 48px 0;
	}

	@media screen and (min-width : $break-desktop) {
	}

	.container {
		max-width : none;
	}
}

.services__head {

	display         : flex;
	justify-content : space-between;
	margin-bottom   : 24px;

	@media screen and (min-width : $break-table) {
		align-items   : flex-start;
		margin-bottom : 48px;
	}

	@media screen and (min-width : $break-desktop) {
		align-items : center;
	}
}

.services__nav {
	align-items : center;
	display     : flex;


	.swiper-pagination {
		bottom    : 16px;
		display   : flex;
		left      : 50%;
		position  : absolute;
		transform : translateX(-50%);

		@media screen and (min-width : $break-table) {
			margin-right : 48px;
			position     : unset;
			transform    : translateX(0);
		}
	}

	.swiper-pagination-bullet {
		border-color : $primary;
	}

	.swiper-pagination-bullet.swiper-pagination-bullet-active {
		background-color : $primary;
	}

	.swiper-nav {
		display : none;

		@media screen and (min-width : $break-table) {
			display : flex;
		}
	}
}

.services__left {
	display        : flex;
	flex-direction : column;

	@media screen and (min-width : $break-desktop) {
		align-items    : center;
		flex-direction : row;
	}
}

.services__title {
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 48px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 0;
		margin-right  : 39px;
	}
}

.services__item {
	background-color : $white;
	border-radius    : 12px;
	display          : flex;
	flex-direction   : column;
	height           : auto;
	margin           : 0 12px;
	padding          : 16px 16px;
	width            : 100%;

	@media screen and (min-width : $break-table) {
		padding : 24px 24px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	.b-btn--dark {
		font-size   : 10px;
		line-height : 16px;
		margin-top  : auto;
		width       : 100%;

		@media screen and (min-width : $break-table) {
			font-size   : 16px;
			line-height : 24px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.services__icon {
	height        : 40px;
	margin-bottom : 24px;
	width         : 40px;

	@media screen and (min-width : $break-table) {
		height        : 48px;
		margin-bottom : 48px;
		width         : 48px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.services__name {
	color          : $primary;
	font-size      : 16px;
	font-weight    : 500;
	line-height    : 22px;
	margin-bottom  : 16px;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {
		font-size     : 22px;
		line-height   : 32px;
		margin-bottom : 16px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.services__list {
	margin-bottom : 24px;

	li {
		color       : $primary;
		font-size   : 12px;
		font-weight : 500;
		line-height : 24px;

		@media screen and (min-width : $break-table) {
			font-size   : 14px;
			line-height : 32px;
		}

		@media screen and (min-width : $break-desktop) {

		}

		&:not(:last-child) {
			margin-bottom : 0;
		}
	}
}


.swiper--services {
	margin : 0 -12px;

	.swiper-slide {
		display     : flex;
		flex-shrink : 0;
		height      : auto;
		width       : 100%;

		@media screen and (min-width : $break-table) {
			width : 50%;
		}

		@media screen and (min-width : $break-desktop) {
			width : 33.33333%;
		}

		@media screen and (min-width : 1400px) {
			width : 25%;
		}
	}
}
