.hero {
	background-color : $secondary;
	border-radius    : 0 0 12px 12px;
	padding          : 24px 0;

	@media screen and (min-width : $break-table) {
		padding : 40px 0 48px 0;
	}
	@media screen and (min-width : $break-desktop) {

	}
}

.hero__bottom {
	display        : flex;
	flex-direction : column;
	padding-top    : 24px;

	@media screen and (min-width : $break-table) {
		align-items     : center;
		flex-direction  : row;
		justify-content : space-between;
		padding-top     : 40px;
	}
	@media screen and (min-width : $break-desktop) {
		padding-top : 50px;
	}
}

.hero__title {
	color          : $primary;
	font-size      : 13px;
	font-weight    : 500;
	line-height    : 16px;
	margin-bottom  : 24px;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {
		font-size     : 14px;
		line-height   : 20px;
		margin-bottom : 0;
	}
	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 24px;
	}
}

.hero__list {
	align-items : center;
	display     : flex;

	li {
		color          : $primary;
		font-size      : 13px;
		font-weight    : 500;
		line-height    : 16px;
		text-transform : uppercase;

		&:not(:first-child) {
			margin-left : 9px;
		}

		@media screen and (min-width : $break-table) {
			font-size   : 14px;
			line-height : 20px;

			&:not(:first-child) {
				margin-left : 29px;
			}

		}
		@media screen and (min-width : $break-desktop) {
			font-size   : 16px;
			line-height : 24px;
		}

	}
}

.hero-slider {
	background-color : $primary;
	border-radius    : 12px;
	margin-top       : 8px;
	position         : relative;

	.container {
		max-width : none;
	}

	.swiper-nav {
		display : none;

		@media screen and (min-width : $break-table) {
			bottom   : 48px;
			display  : flex;
			position : absolute;
			right    : 48px;
			z-index  : 20;
		}
		@media screen and (min-width : $break-desktop) {

		}
	}

	.swiper-pagination {
		bottom    : -26px;
		left      : 50%;
		position  : absolute;
		transform : translateX(-50%);
		z-index   : 1;

		.swiper-pagination-bullet {
			border : 1px solid #93A1A3;

			@media screen and (min-width : $break-table) {
				border : 1px solid $secondary;
			}
		}

		.swiper-pagination-bullet-active {
			background-color : $primary;

			@media screen and (min-width : $break-table) {
				background-color : $secondary;
			}
		}

		@media screen and (min-width : $break-table) {
			bottom    : unset;
			left      : unset;
			right     : 48px;
			top       : 48px;
			transform : translateX(0);
		}
		@media screen and (min-width : $break-desktop) {

		}
	}
}

.hero-slide {
	display         : flex;
	justify-content : space-between;

	@media screen and (min-width : $break-table) {

		justify-content : initial;
		width           : 100%;
	}

	&__wrap {
		margin-bottom : 24px;

		@media screen and (min-width : $break-table) {
			margin-bottom : 0;
		}
	}

	&__info {
		align-items     : flex-start;
		display         : flex;
		flex-direction  : column;
		justify-content : space-between;
		padding         : 24px 0;

		.b-btn--white {
			font-size   : 10px;
			line-height : 16px;
			padding     : 12px 13px;
		}

		@media screen and (min-width : $break-table) {
			padding : 48px 0;
			width   : 50%;
			.b-btn--white {
				font-size   : 14px;
				line-height : 20px;
				padding     : 12px 26px;
			}
		}
		@media screen and (min-width : $break-desktop) {
			max-width : none;
			width     : 38%;
		}
	}

	&__title {
		color          : $white;
		font-size      : 16px;
		font-weight    : 500;
		line-height    : 22px;
		margin-bottom  : 16px;
		text-transform : uppercase;

		@media screen and (min-width : $break-table) {
			font-size   : 24px;
			line-height : 32px;
		}
		@media screen and (min-width : $break-desktop) {
			font-size     : 32px;
			line-height   : 40px;
			margin-bottom : 32px;
		}
	}

	&__text {
		color         : $white;
		font-size     : 12px;
		font-weight   : 500;
		line-height   : 16px;
		margin-bottom : 24px;

		@media screen and (min-width : $break-table) {
			font-size     : 14px;
			line-height   : 20px;
			margin-bottom : 32px;
			max-width     : 343px;
		}
		@media screen and (min-width : $break-desktop) {
			font-size   : 16px;
			line-height : 24px;
			max-width   : 388px;
		}
	}

	&__image {
		display         : flex;
		flex-direction  : column;
		justify-content : flex-end;

		@media screen and (min-width : $break-table) {
			height : 340px;
		}

		@media screen and (min-width : $break-desktop) {
			height      : 572px;
			margin-left : 76px;
			margin-top  : -208px;
		}

		img {
			max-width : none;
			width     : 98px;
			@media screen and (min-width : $break-table) {
				margin-left : 10px;
				max-width   : none;
				width       : 217px;
			}
			@media screen and (min-width : $break-desktop) {
				width : 337px;
			}
		}
	}
}

.swiper--hero {

	@media screen and (min-width : $break-table) {
		overflow : visible;
	}
	@media screen and (min-width : $break-desktop) {

	}

	.swiper-slide {
		width : 100%;

		@media screen and (min-width : $break-table) {

		}
	}
}


