.licenses {
	background-color : $secondary;
	margin-top       : 32px;
	padding          : 24px 0;

	@media screen and (min-width : $break-table) {
		padding : 48px 0;
	}

	@media screen and (min-width : $break-desktop) {
		margin-top : 8px;
	}

	.container {
		max-width : none;
	}
}

.licenses__wrapper {

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		display         : flex;
		justify-content : space-between;
	}
}

.licenses__left {
	margin-bottom : 24px;
	@media screen and (min-width : $break-table) {
		display       : flex;
		margin-bottom : 48px;
	}

	@media screen and (min-width : $break-desktop) {
		display       : unset;
		margin-bottom : 0;
		width         : 53%;
	}
}

.licenses__title {
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 38px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.licenses__image {
	left     : 21%;
	position : relative;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		left       : unset;
		text-align : center;
	}
}

.licenses__list {


	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		width : 47%;
	}
}

.licenses__item {
	transition : box-shadow .3s ease;

	&:hover {
		box-shadow : 0 10px 15px rgba(0, 59, 74, 0.1);
	}

	&:not(:last-child) {
		margin-bottom : 8px;
	}

	&.disabled {
		pointer-events : none;

		img {
			opacity : 0.5;
		}
	}
}

.licenses__download {
	align-items      : center;
	background-color : $white;
	border-radius    : 12px;
	display          : flex;
	padding          : 16px 16px;
	width            : 100%;

	@media screen and (min-width : $break-table) {
		padding : 24px 24px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.licenses__wrap {
	align-items    : flex-start;
	display        : flex;
	flex           : 1 0 auto;
	flex-direction : column;
	margin         : 0 16px;

	@media screen and (min-width : $break-table) {

		align-items     : center;
		flex-direction  : row;
		justify-content : space-between;
		margin          : 0 24px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.licenses__icon {
	flex-shrink : 0;
	height      : 40px;
	width       : 40px;

	@media screen and (min-width : $break-table) {
		height : 48px;
		width  : 48px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.licenses__name {
	color       : $primary;
	font-size   : 10px;
	font-weight : 600;
	line-height : 12px;


	@media screen and (min-width : $break-table) {
		font-size   : 16px;
		line-height : 24px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.licenses__size {
	color       : $primary;
	font-size   : 9px;
	font-weight : 600;
	line-height : 12px;
	opacity     : 0.6;

	@media screen and (min-width : $break-table) {
		font-size   : 12px;
		line-height : 15px;
	}
}
