.disable-scroll {
	height   : 100vh;
	left     : 0;
	overflow : hidden;
	position : fixed;
	top      : 0;
	width    : 100%;
}

.graph-modal {
	background-color  : rgba(0, 0, 0, 0.7);
	bottom            : 0;
	cursor            : pointer;
	left              : 0;
	opacity           : 0;
	overflow-x        : hidden;
	overflow-y        : auto;
	position          : fixed;
	right             : 0;
	text-align        : center;
	--transition-time : 0.3s;
	top               : 0;
	transition        : opacity var(--transition-time), visibility var(--transition-time);
	visibility        : hidden;
	z-index           : 1000;
}

.graph-modal::before {
	content        : "";
	display        : inline-block;
	height         : 100%;
	vertical-align : middle;
}

.graph-modal__container {
	background-color : #FFF;
	border-radius    : 12px;
	cursor           : default;
	display          : none;
	margin           : 50px auto;
	padding          : 24px;
	position         : relative;
	text-align       : left;
	vertical-align   : middle;
	width            : 256px;

	@media screen and (min-width : $break-table) {
		padding : 48px;
		width   : 534px;
	}
	@media screen and (min-width : $break-desktop) {
		padding : 64px;
		width   : 760px;
	}
}

@media (max-width : 1024px) {
	.graph-modal__container {
		width : 90%;
	}
}

.graph-modal__close {
	background-color : transparent;
	border           : none;
	cursor           : pointer;
	height           : 21px;
	padding          : 0;
	position         : absolute;
	right            : 24px;
	top              : 24px;
	width            : 21px;

	svg {
		circle {
			fill       : transparent;
			transition : fill .3s ease;
		}
	}

	&:hover {
		svg {
			circle {
				fill       : #CDE8EA;
				transition : fill .3s ease;
			}
		}
	}
}

.graph-modal.is-open {
	opacity    : 1;
	transition : opacity var(--transition-time), visibility var(--transition-time);
	visibility : visible;
}

.graph-modal__container.graph-modal-open {
	display : inline-block;
}

.fade {
	opacity    : 0;
	transition : opacity var(--transition-time);
}

.fade.animate-open {
	opacity    : 1;
	transition : opacity var(--transition-time);
}

.fadeInUp {
	opacity    : 0;
	transform  : translateY(-100px);
	transition : transform var(--transition-time), opacity var(--transition-time);
}

.fadeInUp.animate-open {
	opacity    : 1;
	transform  : translateY(0);
	transition : transform var(--transition-time), opacity var(--transition-time);
}
