* {
	-webkit-tap-highlight-color : rgba(0, 0, 0, 0);
}

html {
	box-sizing : border-box;
	height     : 100%;
}

*,
*::before,
*::after {
	box-sizing : inherit;
}

.page-wrapper {
	display        : flex;
	flex-direction : column;
	margin         : 0 auto;
	min-height     : 100%;
	overflow       : hidden;
	padding        : 16px 16px;
	position       : relative;
	@media screen and (min-width : $break-table) {
		padding : 24px 24px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.main {
	flex : 1 0 auto;
}

.container {
	-moz-box-sizing    : border-box;
	-webkit-box-sizing : border-box;
	box-sizing         : border-box;
	margin             : 0 auto;
	max-width          : 1060px;
	padding            : 0 16px;
	width              : 100%;

	@media screen and (min-width : $break-table) {
		max-width : 1153px;
		padding   : 0 48px;
	}
	@media screen and (min-width : $break-desktop) {
		max-width : 1217px;
		padding   : 0 64px;
	}
}
